import { Component, Element, Event, EventEmitter, h, Host, Prop } from '@stencil/core';
import { SharedStyles } from '../../utils/shared-styles';
import { StormButtonGroupDataSet } from './storm-button-group-data';

@Component({
  tag: 'storm-button-group',
  styleUrl: 'storm-button-group.css',
  shadow: true,
})
export class StormButtonGroup {
  @Element() host: HTMLElement;
  @Prop() dataSet: StormButtonGroupDataSet[];

  @Event() itemClick: EventEmitter<StormButtonGroupDataSet>;

  onClick = async data => {
    this.itemClick.emit(data);
  };

  componentWillLoad() {
    SharedStyles.applyShadowRootStyles(this.host);
  }

  render() {
    return (
      <Host>
        <div class="d-sm-flex justify-content-between">
          <div class="d-flex">
            {this.dataSet.map(data => (
              <button class={`${data.class}`} type="button" onClick={() => this.onClick(data)}>
                {data.label}
              </button>
            ))}
          </div>
        </div>
      </Host>
    );
  }
}
